import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "GameSortConf",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    return {
      value: true,
      labelPosition: "left", //左对齐
      dialogFormVisible: false,
      searchChannel: "", //table页面渠道列表
      funOpts: [
        { value: "1", label: "留空" },
        { value: "2", label: "活动" },
        { value: "3", label: "邮件" },
        { value: "4", label: "排行榜" },
        { value: "5", label: "设置" },
        { value: "6", label: "保险箱" },
        { value: "7", label: "广播" },
        { value: "8", label: "财神" }
      ],
      gameOpts: [],
      tableData: [],
      form: {
        id: null,
        checkList: [],
        game_1_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_2_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_3_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_4_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_5_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_6_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_7_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_8_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_9_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_10_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        }
      },
      gameList: {},
      channelOpts: [],
      allChannelOpts: []
    };
  },
  methods: {
    resetForm() {
      this.form = {
        id: null,
        checkList: [],
        game_1_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_2_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_3_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_4_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_5_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_6_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_7_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_8_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_9_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        },
        game_10_obj: {
          game_des: "",
          corner_remark: "0",
          status: "0",
          leader: "1"
        }
      };
    },
    sendDataToServer() {
      let data = {
        type_id: 4
      };
      this.$http.post("v1/backend/lobby/server_config_two", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.$message({
            type: "success",
            message: res.data.msg
          });
        }
      });
    },
    getGameSortList() {
      this.$http
        .get("v1/backend/lobby/game_sort", {
          params: {
            page: 1,
            limit: 10
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.tableData = res.data.data;
            this.game_list_index();
            console.log(this.tableData);
          }
        });
    },
    game_list_index: function() {
      this.tableData.map(item => {
        var gameList = JSON.parse(item.game_list);
        this.gameList = JSON.parse(item.game_list);
        for (var key in gameList) {
          item[`game_${key}`] = gameList[key].game_type;
        }
      });
    },
    getGameList() {
      this.$http
        .get("v1/backend/lobby/all_game_list", {
          params: {
            page: this.currentPage,
            limit: this.pagesize,
            type_id: 1
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.gameOpts = res.data.data.slice(1);
          }
        });
    },
    //获取新增页面的渠道列表
    getAddChannelList() {
      let data = {
        type_id: 1,
        add_id: 2
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.channelOpts = res.data.data;
        }
      });
    },
    //获取编辑页面的渠道列表
    getEditChannelList() {
      let data = {
        type_id: 2,
        add_id: 2
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.channelOpts = res.data.data;
        }
      });
    },
    //获取列表页的所有渠道
    getAllChannelList() {
      let data = {
        type_id: 2,
        add_id: 2
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.allChannelOpts = res.data.data;
        }
      });
    },
    openAddDialog() {
      this.dialogFormVisible = true;
      this.getAddChannelList();
      this.getGameList();
      this.resetForm();
    },
    addGameSortConf() {
      if (!this.form.id) {
        let gameList = {
          "1": {
            position: 1,
            game_type: this.form.game_1_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_1_obj.corner_remark,
            status: this.form.game_1_obj.status,
            guide_status: this.form.game_1_obj.leader
          },
          "2": {
            position: 2,
            game_type: this.form.game_2_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_2_obj.corner_remark,
            status: this.form.game_2_obj.status,
            guide_status: this.form.game_2_obj.leader
          },
          "3": {
            position: 3,
            game_type: this.form.game_3_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_3_obj.corner_remark,
            status: this.form.game_3_obj.status,
            guide_status: this.form.game_3_obj.leader
          },
          "4": {
            position: 4,
            game_type: this.form.game_4_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_4_obj.corner_remark,
            status: this.form.game_4_obj.status,
            guide_status: this.form.game_4_obj.leader
          },
          "5": {
            position: 5,
            game_type: this.form.game_5_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_5_obj.corner_remark,
            status: this.form.game_5_obj.status,
            guide_status: this.form.game_5_obj.leader
          },
          "6": {
            position: 6,
            game_type: this.form.game_6_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_6_obj.corner_remark,
            status: this.form.game_6_obj.status,
            guide_status: this.form.game_6_obj.leader
          },
          "7": {
            position: 7,
            game_type: this.form.game_7_obj.game_des,
            shown_type: 7,
            notice_type: this.form.game_7_obj.corner_remark,
            status: this.form.game_7_obj.status,
            guide_status: this.form.game_7_obj.leader
          },
          "8": {
            position: 8,
            game_type: this.form.game_8_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_8_obj.corner_remark,
            status: this.form.game_8_obj.status,
            guide_status: this.form.game_8_obj.leader
          },
          "9": {
            position: 9,
            game_type: this.form.game_9_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_9_obj.corner_remark,
            status: this.form.game_9_obj.status,
            guide_status: this.form.game_9_obj.leader
          },
          "10": {
            position: 10,
            game_type: this.form.game_10_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_10_obj.corner_remark,
            status: this.form.game_10_obj.status,
            guide_status: this.form.game_10_obj.leader
          }
        };
        let data = {
          name: String(this.form.checkList),
          code: String(this.form.checkList),
          game_list: JSON.stringify(gameList)
        };
        this.$http.post("v1/backend/lobby/game_sort", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getGameSortList();
          }
        });
      } else {
        let gameList = {
          "1": {
            position: 1,
            game_type: this.form.game_1_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_1_obj.corner_remark,
            status: this.form.game_1_obj.status,
            guide_status: this.form.game_1_obj.leader
          },
          "2": {
            position: 2,
            game_type: this.form.game_2_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_2_obj.corner_remark,
            status: this.form.game_2_obj.status,
            guide_status: this.form.game_2_obj.leader
          },
          "3": {
            position: 3,
            game_type: this.form.game_3_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_3_obj.corner_remark,
            status: this.form.game_3_obj.status,
            guide_status: this.form.game_3_obj.leader
          },
          "4": {
            position: 4,
            game_type: this.form.game_4_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_4_obj.corner_remark,
            status: this.form.game_4_obj.status,
            guide_status: this.form.game_4_obj.leader
          },
          "5": {
            position: 5,
            game_type: this.form.game_5_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_5_obj.corner_remark,
            status: this.form.game_5_obj.status,
            guide_status: this.form.game_5_obj.leader
          },
          "6": {
            position: 6,
            game_type: this.form.game_6_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_6_obj.corner_remark,
            status: this.form.game_6_obj.status,
            guide_status: this.form.game_6_obj.leader
          },
          "7": {
            position: 7,
            game_type: this.form.game_7_obj.game_des,
            shown_type: 7,
            notice_type: this.form.game_7_obj.corner_remark,
            status: this.form.game_7_obj.status,
            guide_status: this.form.game_7_obj.leader
          },
          "8": {
            position: 8,
            game_type: this.form.game_8_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_8_obj.corner_remark,
            status: this.form.game_8_obj.status,
            guide_status: this.form.game_8_obj.leader
          },
          "9": {
            position: 9,
            game_type: this.form.game_9_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_9_obj.corner_remark,
            status: this.form.game_9_obj.status,
            guide_status: this.form.game_9_obj.leader
          },
          "10": {
            position: 10,
            game_type: this.form.game_10_obj.game_des,
            shown_type: 1,
            notice_type: this.form.game_10_obj.corner_remark,
            status: this.form.game_10_obj.status,
            guide_status: this.form.game_10_obj.leader
          }
        };
        let data = {
          id: this.form.id,
          name: String(this.form.checkList),
          code: String(this.form.checkList),
          game_list: JSON.stringify(gameList)
        };
        this.$http.put("v1/backend/lobby/game_sort", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getGameSortList();
          }
        });
      }
    },
    handleEdit(row) {
      console.log(row);
      this.getEditChannelList();
      this.getGameList();
      this.dialogFormVisible = true;
      this.form.id = row.id;
      this.form.checkList = [row.channel_name];
      let game_list = JSON.parse(row.game_list);
      console.log(game_list);
      for (var key in game_list) {
        this.form[`game_${key}_obj`] = {
          game_des: game_list[key].game_type,
          corner_remark: game_list[key].notice_type,
          status: game_list[key].status,
          leader: game_list[key].guide_status
        };
      }
    },
    handleDelete(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("v1/backend/lobby/game_sort", {
              params: {
                id: row.id
              }
            })
            .then(res => {
              if (res.data.code === 1) {
                this.getGameSortList();
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  },
  mounted() {
    this.getGameSortList();
    this.getAllChannelList();
  }
};



